.footer {
	background-color: #343840;
	padding: 40px 0px 25px; }

.footer__list {

	.footer__list-categorie {
		color: #fff;
		font-size: 16px;
		font-family: $helveticanuen-bold;
		font-weight: 700; }

	.footer__catalog li {
		margin: 5px 0px;

		.footer__catalog-link {
			font-family: $helveticanuen;
			font-size: 13px;
			color: #676f80;
			font-weight: 400;
			text-decoration: none;
			padding-left: 16px;
			position: relative;
			&::before {
				content: '';
				display: inline-block;
				@include s('footer-arrow');
				position: relative;
				top: -2px;
				left: -6px; }
			&:hover {
				color: #bdc2ce; }
			&:hover::before {
				@include s('footer-arrow-hover'); } } }

	.footer__catalog-social {
		width: 160px;
		margin-top: 5px;
		@include r(950) {
			margin: 0 auto; }

		.footer__catalog-social-link {
			width: 40px;
			height: 40px;
			border: 2px solid #3b4049;
			border-radius: 50%;
			float: left;
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 6px;
			transition: all .5s;
			span {
				opacity: .2;
				transition: opacity .5s; }
			&:hover span {
				opacity: 1; }
			@include r(480) {
				margin: 5px; } }

		// hover collection

		$select-name: facebook #3b5998, twitter #00aced, in #0977b5, google #cc3732, pinterest #cc2127, instagram #3f729b;

		.footer__catalog-social-link {
			@each $name, $color in $select-name {
				&_#{$name}:hover {
					background-color: $color;
					border: 2px solid $color; } } } } }

// copyrights

.copyright {
	display: table;
	padding: 38px 0px 20px;
	width: 100%;

	.copyright__left-rights {
		font-family: $helveticanuen;
		font-weight: 400;
		color: #676f80;
		font-size: 12px; }

	.copyright__right-policy {
		color: #676f80;
		font-size: 12px;
		font-family: $helveticanuen;
		font-weight: 400; }

	.copyright__right-terms {
		@extend .copyright__right-policy; } }
