.app-tabs__list {
	font-size: 0;

	.app-tabs__list-point {
		width: 33.3%;
		display: inline-block;
		text-align: center;
		border-bottom: 1px solid #dae8f2;
		cursor: pointer;
		padding: 8px 0px;
		background-color: #fff;
		&.point-is-active {
			position: relative;
			transition: all .5s;
			&::after {
				content: '';
				display: block;
				height: 3px;
				width: 100%;
				background-color: #528cc2;
				position: absolute;
				bottom: -1px;
				z-index: 3; }
			.icon-png {
				opacity: 1;
				transition: all .5s; }

			.app-tabs__list-point-title {
				color: #528cc2; } }

		.icon-png {
			opacity: .2;
			margin-right: 6px; }
		&:first-child {
			border-top-left-radius: 10px; }
		&:nth-child(2) {
			border-left: 1px solid #dae8f2;
			border-right: 1px solid #dae8f2; }
		&:last-child {
			border-top-right-radius: 10px; }

		.app-tabs__list-point-title {
			position: relative;
			top: -8px;
			opacity: 1;
			color: #9faeb8;
			font-size: 13px;
			font-family: $helveticanuen-mid;
			font-weight: 500; } } }

// tabs content

.app-tabs__content {
	background-color: #fcfdff;
	border-bottom-right-radius: 10px;
	border-bottom-left-radius: 10px; }

// build tab

.build {
	background-image: url("../img/tab-build.png");
	background-repeat: no-repeat;
	background-position: center 30px;
	height: 324px;
	position: relative;
	&::before {
		content: '';
		position: absolute;
		bottom: -17px;
		left: -57px;
		background-image: url("../img/tabs-drag.png");
		height: 102px;
		width: 119px;
		@include r(950) {
			left: 0px; } }
	@include r(420) {
		background-size: 100%; } }


// build tab

